/* Core */
import { Component, NgZone, OnInit, ViewEncapsulation } from '@angular/core';
import { IonicPage, NavController, PopoverController } from 'ionic-angular';
import { NavParams } from 'ionic-angular';
import { DomSanitizer } from '@angular/platform-browser';

/* Services */
import { ClipsService } from '../../app/services/clips.service';
import { LoaderService } from '../../app/services/loader.service';
import { ToastService } from '../../app/services/toast.service';
import { ClipUserInfo } from '../../app/models/clip-user-info.model';
import { AuthService } from '../../app/services/auth.service';
import { ClipPublicInfo } from '../../app/models/clip-publicinfo.model';

import { environment } from '../../../environments/environment';
import { LocalStorageService } from '../../app/services/localStorage.service';
import { ClipboardService } from 'ngx-clipboard';
import { UserComment } from '../../app/models/user-comment.model';

@IonicPage({
  defaultHistory: ['trending'],
  name: 'clip',
  segment: 'clip/:slug'
})
@Component({
  selector: 'page-clip',
  templateUrl: 'clip.html',
  styles: ['.no-padding {padding: 0;} .no-margin {margin: 0;}']
})
export class ClipPage implements OnInit {
	public clip: any;
  public comments: UserComment[];
  public commentsCount: number = 0;
  public isFavorite: boolean = false;
  public isLiked: boolean = false;
  public likesCount: number = 0;
  public slug: string;
  public userComment: string;

  public cropInfo = "";
  public selectClipFeature = false;

	constructor(
    private navCtrl: NavController,
    private navParams: NavParams,
    private _ngZone: NgZone,
    private _sanitizer: DomSanitizer,
    private popoverCtrl: PopoverController,
    private clipboardService: ClipboardService,

    /* Services */
    private authService: AuthService,
    private clipsService: ClipsService,
    private loaderService: LoaderService,
    private localStorageService: LocalStorageService,
    private toastService: ToastService) {
		let slug = navParams.get('slug');
		this.slug = slug;

    if (!environment.production) {
      this.selectClipFeature = true;
    }
	}

	ngOnInit() {
    this.getBySlug();

    this.getClipPublicInfo();
    this.getComments();

    if (this.authService.isAuthenticated()) {
        this.getClipUserInfo();
    }
	}

  async addComment() {
    let that = this;

    if (!this.authService.isAuthenticated()) {
      this.toastService.show('Must be logged in to comment on a clip.');
      return;
    }

    let response = await this.clipsService.addComment(this.slug, this.userComment);

    this._ngZone.run(() => {
      that.comments.push(response.response[0]);
      that.userComment = null;
    });
  }

  like() {
    let that = this;

    if (!that.authService.isAuthenticated()) {
      that.toastService.show('Must be logged in to like a clip.');
      return;
    }

    let promise = this.clipsService.like(this.slug).then(() => {
      that._ngZone.run(() => {
          that.isLiked = true;
          that.likesCount++;
      });
    });

    this.toastService.onFailure(promise);
  }

  unlike() {
    let that = this;
    let promise = this.clipsService.unlike(this.slug).then(() => {
      that._ngZone.run(() => {
          that.isLiked = false;
          that.likesCount--;
      });
    });

    this.toastService.onFailure(promise);
  }

  getComments() {
    let promise;
    let that = this;

    promise = this.clipsService.getComments(this.slug).then((comments: any) => {
        that._ngZone.run(() => {
          that.comments = comments;
        });
    });

    that.toastService.onFailure(promise);
  }

  getClipPublicInfo() {
    let promise;
    let that = this;

    promise = this.clipsService.getPublicInfo(this.slug).then((clipPublicInfo: ClipPublicInfo) => {
        that._ngZone.run(() => {
          that.commentsCount = clipPublicInfo.commentsCount;
          that.likesCount = clipPublicInfo.likesCount;
        });
    });

    that.toastService.onFailure(promise);
  }

  getClipUserInfo() {
    let promise;
    let that = this;

    promise = this.clipsService.getUserInfo(this.slug).then((clipUserInfo: ClipUserInfo) => {
        that._ngZone.run(() => {
          that.isFavorite = clipUserInfo.isFavorite;
          that.isLiked = clipUserInfo.isLiked;
        });
    });

    that.toastService.onFailure(promise);
  }

  getBySlug() {
    let promise;
    let that = this;

    promise = this.clipsService.getBySlug(this.slug).then(function(clip) {
    let src = 'https://clips.twitch.tv/embed?clip=' + clip.slug + '&tt_medium=clips_api&tt_content=embed&autoplay=true';
         clip.embedHtml = that._sanitizer.bypassSecurityTrustHtml("<div style='overflow: hidden; padding-bottom: 56.25%; position: relative; height: 0;' class='video-responsive'><iframe style='left: 0; top: 0; height: 100%; width: 100%; position: absolute;' src='" + src + "' width='100%' height='100%' muted='false' frameborder='0' scrolling='no' allowfullscreen='true' preload='metadata'></iframe></div>");

        that._ngZone.run(() => {
          that.clip = clip;
        });
    });

    that.loaderService.show(promise);
    that.toastService.onFailure(promise);
  }

  addFavorite() {
    let that = this;
    let promise = this.clipsService.addFavorite(this.slug).then(function() {
      that._ngZone.run(() => {
          that.isFavorite = true;
          that.toastService.show('Added clip to favorites.');
      });
    });

    this.toastService.onFailure(promise);
  }

  removeFavorite() {
    let that = this;
    let promise = this.clipsService.removeFavorite(this.slug).then(function() {
      that._ngZone.run(() => {
          that.isFavorite = false;
          that.toastService.show('Removed clip from favorites.');
      });
    });

    this.toastService.onFailure(promise);
  }

  goToChannel() {
    let that = this;

    that.navCtrl.push('trending-channel', {channel: that.clip.broadcaster.displayName});
  }

  presentPopover(myEvent) {
    let that = this;
    let popover = this.popoverCtrl.create('clip-popover', {slug: that.slug});
    popover.present({
      ev: myEvent
    });
  }

  goToProfile() {
    this.navCtrl.push('profile');
  }

  /* Local only methods */
  clearSelectedClips() {
    this.localStorageService.setItem('selectedClips', '');

    this.toastService.show('Cleared selected clips.');
  }

  selectClip() {
    let found = false;
    let selectedClipsString = this.localStorageService.getItem('selectedClips');
    let selectedClips = [];
    let that = this;

    let slug = that.slug;

    if (that.cropInfo) {
      slug += `:${that.cropInfo}`;
    }

    console.log(that.cropInfo);

    if (!selectedClipsString) {
      selectedClips = [];
    } else {
      selectedClips = JSON.parse(selectedClipsString);
    }

    selectedClips.forEach(function(selectedClip) {
      if (selectedClip === slug) {
        found = true;
      }
    });

    if (!found) {
        selectedClips.push(slug);
    }

    this.localStorageService.setItem('selectedClips', JSON.stringify(selectedClips));

    this.toastService.show('Added clip to selected clips.');
  }

  copySelectedClips() {
    this.clipboardService.copyFromContent(JSON.parse(this.localStorageService.getItem('selectedClips')).join(','));
    this.toastService.show('Copied selected clips to clipboard.');
  }
}



